import axios from "axios";
import React, { Component } from "react";
import { Link } from "react-router-dom";
import "../../../assets/side-panel-styles/sidePanel.css";

export default class SidePanel extends Component {
  constructor() {
    super();

    this.state = {
      admin: undefined,
    };
  }

  componentDidMount = async () => {
    try {
      const token = localStorage.getItem("auth-token");
      const tokenRes = await axios.post(process.env.REACT_APP_API_URL +"/api/admin/tokenIsValid", null, {
        headers: { "x-auth-token": token },
      });

      if (tokenRes.data) {
        //logged in
        const adminRes = await axios.get(process.env.REACT_APP_API_URL +"/api/admin", {
          headers: { "x-auth-token": token },
        });
        console.log("admin profile: ", adminRes.data.user);
        this.setState({
          admin: adminRes.data.user,
        });
      }
    } catch (e) {
      console.log(e);
    }
  };

  render() {
    const currLocation = window.location.href.split("#/")[1];
    return (
      <div className="mt-4">
        {/* stats*/}
        <Link to="/statistics" style={{ textDecoration: "none" }}>
          <li className="list-group-item text-dark border-0 my-1 myList">
            <i
              className="fas fa-chart-bar mr-4"
              style={{ fontSize: "20px" }}
            ></i>{" "}
            {currLocation === "statistics" || currLocation === "" ? (
              <b>Dashboard</b>
            ) : (
              "Dashboard"
            )}
          </li>
        </Link>

        {/* add branch 
        <Link to="/addBranch" style={{ textDecoration: "none" }}>
            <li className="list-group-item text-dark border-0 my-1 myList">
              <i
                className="fas fa-user-plus mr-4"
                style={{ fontSize: "20px" }}
              ></i>
              {currLocation === "addBranch" ? <b>Add Branch</b> : "Add Branch"}
            </li>
          </Link>
      */}
          {/* view branch */}
          <Link to="/viewBranches" style={{ textDecoration: "none" }}>
            <li className="list-group-item text-dark border-0 my-1 myList">
              <i
                className="fas fa-search mr-4"
                style={{ fontSize: "20px" }}
              ></i>
              {currLocation === "viewBranches" ? (
                <b>View Branch</b>
              ) : (
                "View Branch"
              )}
            </li>
          </Link>

        {/* add emp */}
        <ul className="list-group">
          {/*
          <Link to="/add" style={{ textDecoration: "none" }}>
            <li className="list-group-item text-dark border-0 my-1 myList">
              <i
                className="fas fa-user-plus mr-4"
                style={{ fontSize: "20px" }}
              ></i>
              {currLocation === "add" ? <b>Add Employee</b> : "Add Employee"}
            </li>
          </Link>
          */}

          {/* view emp */}
          <Link to="/viewEmployees" style={{ textDecoration: "none" }}>
            <li className="list-group-item text-dark border-0 my-1 myList">
              <i
                className="fas fa-search mr-4"
                style={{ fontSize: "20px" }}
              ></i>
              {currLocation === "viewEmployees" ? (
                <b>View Employee</b>
              ) : (
                "View Employee"
              )}
            </li>
          </Link>

        {/* add in ward 
          <Link to="/addInWard" style={{ textDecoration: "none" }}>
            <li className="list-group-item text-dark border-0 my-1 myList">
              <i
                className="fas fa-user-plus mr-4"
                style={{ fontSize: "20px" }}
              ></i>
              {currLocation === "addInWard" ? <b>In-Ward</b> : "In-Ward"}
            </li>
          </Link>
        */}
        {/* view in ward */}
        <Link to="/viewInWards" style={{ textDecoration: "none" }}>
            <li className="list-group-item text-dark border-0 my-1 myList">
              <i
                className="fas fa-search mr-4"
                style={{ fontSize: "20px" }}
              ></i>
              {currLocation === "viewInWards" ? <b>In-Ward Report</b> : "In-Ward Report"}
            </li>
          </Link>

          {/* add sale 
          <Link to="/addSales" style={{ textDecoration: "none" }}>
            <li className="list-group-item text-dark border-0 my-1 myList">
              <i
                className="fas fa-user-plus mr-4"
                style={{ fontSize: "20px" }}
              ></i>
              {currLocation === "addSales" ? <b>Sale</b> : "Sale"}
            </li>
          </Link>
        */}
        {/* view sale */}
        <Link to="/viewSales" style={{ textDecoration: "none" }}>
            <li className="list-group-item text-dark border-0 my-1 myList">
              <i
                className="fas fa-search mr-4"
                style={{ fontSize: "20px" }}
              ></i>
              {currLocation === "viewSales" ? <b>Sales Report</b> : "Sales Report"}
            </li>
          </Link>

          {/* view req
          <Link to="/viewRequests" style={{ textDecoration: "none" }}>
            <li className="list-group-item text-dark border-0 my-1 myList">
              <i
                className="fas fa-comments mr-4"
                style={{ fontSize: "20px" }}
              ></i>
              {currLocation === "viewRequests" ? (
                <b>View Requests</b>
              ) : (
                "View Requests"
              )}
              {this.state.admin &&
              (this.state.admin.leaveRequests.length ||
                this.state.admin.bonusRequests.length ||
                this.state.admin.loanRequests.length) ? (
                <div
                  className="fas fa-circle ml-2"
                  style={{
                    color: "#FF1D15",
                    fontSize: "8px",
                  }}
                ></div>
              ) : null}
            </li>
          </Link>
          */}
          {/* payroll 
          <Link to="/payroll" style={{ textDecoration: "none" }}>
            <li className="list-group-item text-dark border-0 my-1 myList">
              <i
                className="fas fa-file-invoice mr-4"
                style={{ fontSize: "20px" }}
              ></i>{" "}
              {currLocation === "payroll" ? <b>Payroll</b> : "Payroll"}
            </li>
          </Link>
        */}
          {/* Active loans 
          <Link to="/activeLoans" style={{ textDecoration: "none" }}>
            <li className="list-group-item text-dark border-0 my-1 myList">
              <i
                className="fas fa-hand-holding-usd mr-4"
                style={{ fontSize: "20px" }}
              ></i>
              {currLocation === "activeLoans" ? (
                <b>Active Loans</b>
              ) : (
                "Active Loans"
              )}
            </li>
          </Link>
          */}
          {/* options 
          <Link to="/options" style={{ textDecoration: "none" }}>
            <li className="list-group-item text-dark border-0 my-1 myList">
              <i
                className="fas fa-sliders-h mr-4"
                style={{ fontSize: "20px" }}
              ></i>
              {currLocation === "options" ? <b>Options</b> : "Options"}
            </li>
          </Link>
          */}
        </ul>
      </div>
    );
  }
}
